import React from "react";

class BadgeDetails extends React.Component{
    constructor(props){
        super(props)
        this.state={
            badgeId:""
        }
    }
    render(){
        return(
        this.state.badgeId && <p>{this.state.badgeId}</p>
        )   
    }
    componentDidMount(){
        this.fetchData();
    }
    fetchData = ()=>{
        this.setState({badgeId:this.props.match.params.badgeId});
    }
}
export default BadgeDetails;