import React from "react";
import './styles/BadgeListItem.css'
import {Link} from "react-router-dom"
import BadgeListItem from "./BadgesListItem";


class BadgesList extends React.Component{
    render(){
        // const twitterURL = "https://twitter.com/"
        const badgesList = [...this.props.badges].reverse();
                if(badgesList.length === 0){
                    return(
                        <div className="container">
                            <h3> No badges were found </h3>
                            <Link className="btn btn-primary" to="/badges/new"> Create new Badge</Link>
                        </div>  
                    )
                }else{
                    return(
                        <React.Fragment>
                        {badgesList.map((e) =>(
                                <Link key={e.id}to={`/badges/${e.id}`} className="text-reset text-decoration-none BadgeListItem__container container">
                                    <BadgeListItem badge={e}/>
                                </Link>
                        ))}
                        </React.Fragment>
                    )
                }
            }
    }

export default BadgesList;